.tab-content {
    position: relative;
    @include animateIt(.5s);


    & .tab-pane {
        display: block;
        position: absolute;
        

        &-wrapper {
            
            > *:not(:first-child) {
                margin-top: 2.5rem;
            }
    
            .paragraph-big {
                margin-top: 3rem;
            }
    
            .pattern-image {
                margin: 5rem 0 3rem 0;
            }
        }
    }
}

.tab-nav {

    @media (min-width: 992px) {
        transform: translate3d(0, 0, 0) !important;
    }

    @media (max-width: 991px) {
        margin-bottom: 3rem;
    }

    .nav-link {
        background-color: transparent;
        display: flex;
        align-items: center;
        padding: 1.4rem 0 1.7rem 0;

        min-width: 100% !important;

        @include animateIt;

        > span {
            display: flex;
            align-items: center;
        }

        i {
            font-size: 5rem;
            margin-right: 1.5rem;
            color: $font-grey;
            @include animateIt;
            &::before {
                background: #fff;
                @include animateIt;
            }
        }

        .text {
            font-size: 2rem;
            line-height: 3.6rem;
            font-weight: 600;
            color: $font-grey;
            @include animateIt;
        }

        &.active, .show > & {
            background-color: #fff;
            color: initial;
        }

        &.active {
            i {
                @include icon-gradient;
            }
            span {
                color: $font-dark;
            }
        }

        &:not(:first-child ) {
            border-top: .1rem solid $color-grey-2;
        }

        &-placeholder {
            opacity: 0;
        }

    }

