.tab-section {
    .sub-heading {
        display: flex;
        align-items: center;
        margin: 0 auto 1.8rem 0;
        i {
            font-size: 2.4rem;
            margin: 0 .8rem 0 0;
        }
        h5 {
            font-size: 1.4rem;
            font-weight: 700;
            margin: .3rem 0 0 0;

            @media (max-width: 991px) {
                font-size: 1.6rem;
            }
        }
    }

    &-1 {
        .tab-section-wrapper {
            padding-bottom: 15rem;
            @media (max-width: 991px) {
                padding-bottom: 5rem;
            }
        }
    }
}